import React, {Fragment} from 'react';
import PropTypes from 'prop-types';
import MobileApp from '../MobileApp.jsx';

export default class QuestStep extends React.Component {

	static conditions = {
		CVD: "Heart disease/CVD",
		THYROID_DISEASE: "Thyroid disease",
		PCO: "PCO",
		HEPATITIS: "Hepatitis",
		CANCER: "Cancer",
		HYPERTENSION: "Hypertension",
		DIABETES: "Diabetes",
		HIGH_CHOLESTEROL: "High cholesterol"
	};
	static conditionTypes = {
		DIABETES: {
			TYPE_1: "Type 1",
			TYPE_2: "Type 2",
			TYPE_3: "Gestational"
		},
		CANCER: {
			THYROID: "Thyroid",
			CERVICAL: "Cervical",
			LEUKEMIA: "Leukemia",
			LIVER_BILE_DUCTS: "Liver/Bile ducts",
			LYMPHOMA: "Lymphoma",
			BLADDER: "Bladder",
			COLORECTAL: "Colorectal",
			OVARIAN: "Ovarian",
			PANCREATIC: "Pancreatic",
			MELANOMA: "Melanoma",
			BREAST: "Breast",
			KIDNEY: "Kidney",
			PROSTATE: "Prostate",
			TESTICULAR: "Testicular",
			UTERINE: "Uterine",
			LUNG: "Lung"
		}
	};
	static relationships = {
		PARENT: "Parent",
		SIBLING: "Sibling",
		CHILD: "Child"
	};

	getInitialState() {
		return {
			initialized: false,

			conditions: [], // api
			conditionTypes: {}, // api
			relationships: [], // api
			//prescriptions: {}, // api after form submit

			data: {
				age: "",
				weight: "",
				height: "",
				gender: "",
				activityHours: "", // double
				smokingQuestionnaire: {
					minCigarettesSmoked: "",
					maxCigarettesSmoked: "",
					yearsOfSmoking: "",
					smoker: "" // bool
				},
				conditions: [
					/*{
						name: "DIABETES",
						type: "TYPE_1"
					},
					...*/
				],
				familyConditions: [
					/*{
						name: "DIABETES",
						type: "TYPE_1",
						relationshipLevel: "SIBLING",
						ageOfConditionDetection: 201 // :)
					},
					...*/
				]
			},
			hasErrors: false,
			errors: {
				api: null, // exceptionMessages array
				smokingQuestionnaire: {},
				conditions: {},
				familyConditions: {}
			}
		};
	}

	constructor(props) {
		super(props);
		this.state = this.getInitialState();
	}

	componentDidMount() {
		this.props.fetchApi('/conditions')
			.then(result => this.setState({ conditions: result }));
		this.props.fetchApi('/condition-types')
			.then(result => this.setState({ conditionTypes: result }));
		this.props.fetchApi('/relationships')
			.then(result => this.setState({ relationships: result }));

		// setup button for fast form load (prefills dummy data)
		document.getElementById("iphonex-button-right").addEventListener("click", this.setDummyData);

		//this.props.undoRedoShow(true, false);
	}

	componentWillUnmount() {
		document.getElementById("iphonex-button-right").removeEventListener("click", this.setDummyData);
	}

	componentDidUpdate(prevProps, prevState) {
		if(this.state.hasErrors) {
			document.getElementById("iphonex-content").scrollTop = 0;
		}
	}

	setDummyData = () => {
		this.setState({
			data: {"age":43,"weight":90,"height":186,"gender":"M","activityHours":3,"smokingQuestionnaire":{"minCigarettesSmoked":15,"maxCigarettesSmoked":25,"yearsOfSmoking":10,"smoker":true},"conditions":[{"name":"CANCER","type":"OVARIAN"},{"name":"PCO"},{"name":"NA"}],"familyConditions":[{"name":"DIABETES","type":"TYPE_2","relationshipLevel":"SIBLING","ageOfConditionDetection":64},{"name":"THYROID_DISEASE","relationshipLevel":"CHILD","ageOfConditionDetection":10},{"name":"NA"}]}
		});
	}

	validateData() {
		const {data} = this.state;
		const isEmpty = (val) => val === undefined || String(val).trim() === "";

		let isValid = true;
		let errors = this.getInitialState().errors;
		// just set flag
		const setError = () => isValid = false;

		if(isEmpty(data.age))
			setError(errors.age = "Please enter your age");

		if(isEmpty(data.weight))
			setError(errors.weight = "Please enter your weight");

		if(isEmpty(data.height))
			setError(errors.height = "Please enter your height");

		if(isEmpty(data.activityHours))
			setError(errors.activityHours = "Please enter your activity duration");

		if(isEmpty(data.gender))
			setError(errors.gender = "Please clarify your gender");

		if(isEmpty(data.smokingQuestionnaire.smoker))
			setError(errors.smokingQuestionnaire.smoker = "Please clarify if you are a smoker");
		else if(data.smokingQuestionnaire.smoker == true) {
			if(isEmpty(data.smokingQuestionnaire.yearsOfSmoking)) {
				setError(errors.smokingQuestionnaire.yearsOfSmoking = "Please enter your smoking experience duration");
			}
		}

		// conditions[]
		if(
			data.conditions.length === 0
			|| (
				data.conditions[data.conditions.length - 1].name !== "NA"
				&& !data.conditions[data.conditions.length]
				&& data.conditions[data.conditions.length] !== "NA"
			)
		) {
			setError(errors.conditions[data.conditions.length] = { name: "Please select condition or None" });
		}
		for(const [index, item] of Object.entries(data.conditions)) {
			if(this.state.conditionTypes[item.name] && !item.type) {
				setError(errors.conditions[index] = { type: "Please select type of disease" });
			}
		}

		// familyConditions[]
		if(
			data.familyConditions.length === 0
			|| (
				data.familyConditions[data.familyConditions.length - 1].name !== "NA"
				&& !data.familyConditions[data.familyConditions.length]
				&& data.familyConditions[data.familyConditions.length] !== "NA"
			)
		) {
			setError(errors.familyConditions[data.familyConditions.length] = { name: "Please select condition or None" });
		}
		for(const [index, item] of Object.entries(data.familyConditions)) {
			// familyConditions.name == "NA" is just internal for blank select so ignore it
			if(item.name !== "NA") {
				let err = errors.familyConditions[index] = {};

				if(this.state.conditionTypes[item.name] && !item.type)
					setError(err.type = "Please select type of disease");

				if(!item.relationshipLevel) {
					setError(err.relationshipLevel = "Please select relationship");
				}

				if(isEmpty(item.ageOfConditionDetection)) {
					setError(err.ageOfConditionDetection = "Please select disease detection age");
				}
			}
		}


		this.setState({ errors: errors, hasErrors: !isValid });

		// checks if errors object filled with smth in one-depth
		// if object and has keys == some child object has error messages
		// if var is null - it is not an error
		// TODO refactor it nicer
		/*return Object.values(errors).some((val) =>
			val instanceof Object
				? Object.keys(val).length > 0
				: val !== null
		) === false;*/
		return isValid;
	}

	handleSubmit = () => {
		if(this.validateData()) {
			// TODO refactor to remove this hack (used to avoid references of object)
			let data = JSON.parse(JSON.stringify(this.state.data));

			// cast some data for request
			if(data.conditions instanceof Object) {
				// convert object to array and remove items with name=="N/A" bc this is just a dummy and wrong for API
				data.conditions = Object.values(data.conditions).filter(item => item.name !== 'NA');
			}
			if(data.familyConditions instanceof Object) {
				// convert object to array and remove items with name=="N/A" bc this is just a dummy and wrong for API
				data.familyConditions = Object.values(data.familyConditions).filter(item => item.name !== 'NA');
			}

			this.props.fetchApi('/', { method: "POST", data: data })
				.then(result => {
					const {prescriptions, exceptionMessages} = result;
					let globalErrors = [];
					let errors = this.getInitialState().errors;

					if(exceptionMessages && exceptionMessages.length > 0) {
						exceptionMessages.forEach(err => {

							switch(err.messageField) {
								case "AGE":
									errors.age = err.exceptionMessage;
									break;
								case "WEIGHT":
									errors.weight = err.exceptionMessage;
									break;
								case "HEIGHT":
									errors.height = err.exceptionMessage;
									break;
								case "SMOKING_PERIOD":
									errors.smokingQuestionnaire.yearsOfSmoking = err.exceptionMessage;
									break;
								case "DIAGNOSIS_AGE":
									errors.familyConditions[err.index] = { ageOfConditionDetection: err.exceptionMessage };
									break;
								default:
									globalErrors.push(err.exceptionMessage);
									break;
							}

						});
						this.setState({
							hasErrors: true,
							errors: {
								...this.state.errors,
								...errors,
								api: globalErrors
							}});
					}
					else {
						this.props.setStep(MobileApp.STEP_PLAN, { data: prescriptions });
					}
				});
		}
	}

	handleInputChange = (event) => {
		const target = event.target;
		const name = target.name;
		let value = target.type === "checkbox" ? target.checked : target.value;

		// API requires certain types
		if(["age", "weight", "height"].indexOf(name) > -1) {
			value = value === ""? "" : Number.parseInt(value) || 0;
		}
		else if(name === "activityHours") {
			value = value === ""? "" : Number.parseFloat(value) || 0;
		}

		this.setState(prevState => ({
			data: {
				...prevState.data,
				[name]: value
			}
		}));

		this.props.clearFutureHistory();
	}

	handleSmokerInputChange = (event) => {
		let {name, value} = event.target;

		if(name === 'smoker') {
			value = value === 'yes'? true : false;
		}
		if(["yearsOfSmoking", "minCigarettesSmoked", "maxCigarettesSmoked"].indexOf(name) > -1) {
			value = value === ""? "" : Number.parseInt(value) || 0;
		}

		this.setState(prevState => ({
			data: {
				...prevState.data,
				smokingQuestionnaire: {
					...prevState.data.smokingQuestionnaire,
					[name]: value
				}
			}
		}));

		this.props.clearFutureHistory();
	}

	handleConditionsInputChange = (index, name, value) => {
		this.setState(prevState => {
			let newState = {
				...prevState.data.conditions[index],
				[name]: value
			};

			if(name === 'name') {
				// if "name" option then remove trash (dependent option) to select it again if provided
				delete newState.type;
			}

			// cut condition data after "NA" selected
			let conditions = Object.values({ ...prevState.data.conditions, [index]: newState });
			const blankIndex = conditions.findIndex(item => item.name === "NA");
			if(blankIndex > -1) {
				conditions = conditions.slice(0, blankIndex + 1);
			}

			return {
				data: {
					...prevState.data,
					conditions: conditions
				}
			};
		});

		this.props.clearFutureHistory();
	}

	handleFamilyConditionsInputChange = (index, name, value) => {
		this.setState(prevState => {
			if(name === "ageOfConditionDetection") {
				value = value === ""? "" : Number.parseInt(value) || 0;
			}

			let newState = {
				...prevState.data.familyConditions[index],
				[name]: value
			};

			if(name === 'name') {
				// if "name" option then remove trash (dependent option) to select it again if provided
				delete newState.type;
			}

			// cut condition data after "NA" selected
			let familyConditions = Object.values({ ...prevState.data.familyConditions, [index]: newState });
			const blankIndex = familyConditions.findIndex(item => item.name === "NA");
			if(blankIndex > -1) {
				familyConditions = familyConditions.slice(0, blankIndex + 1);
			}

			return {
				data: {
					...prevState.data,
					familyConditions: familyConditions
				}
			};
		});

		this.props.clearFutureHistory();
	}

	renderError(msg) {
		return <div className="error">{msg}</div>;
	}

	renderSmoker() {
		const data = this.state.data.smokingQuestionnaire;
		const {errors} = this.state;

		const content = (
			<Fragment>
				<div className="row">
					<label>Did you smoke within the last 15 years?</label>
					<div className="group-radio">
						<label>
							<input type="radio" name="smoker" value="yes" checked={data.smoker === true} onChange={this.handleSmokerInputChange} />
							<span>Yes</span>
						</label>
						<label>
							<input type="radio" name="smoker" value="no" checked={data.smoker === false} onChange={this.handleSmokerInputChange} />
							<span>No</span>
						</label>
					</div>
					{this.renderError(errors.smokingQuestionnaire.smoker)}
				</div>
				{data.smoker && (
					<Fragment>
						<div className="row">
							<label>
								How many years have you smoked in total?
								<input type="number" min="0" name="yearsOfSmoking" placeholder="Years" value={data.yearsOfSmoking} onChange={this.handleSmokerInputChange} />
								{this.renderError(errors.smokingQuestionnaire.yearsOfSmoking)}
							</label>
						</div>
						<div className="row">
							<label>On average how many cigarettes per day?</label>
							<div className="group-columns">
								<label>
									<input type="number" min="0" name="minCigarettesSmoked" placeholder="Min" value={data.minCigarettesSmoked} onChange={this.handleSmokerInputChange} />
								</label>
								<label>
									<input type="number" min="0" name="maxCigarettesSmoked" placeholder="Max" value={data.maxCigarettesSmoked} onChange={this.handleSmokerInputChange} />
								</label>
							</div>
						</div>
					</Fragment>
				)}
			</Fragment>
		);

		return data.smoker? <fieldset>{content}</fieldset> : content;
	}

	renderConditions(data, errors, handleInputChange, nameFieldLabel, appendContentCallback) {
		const conditionsList = (this.state.conditions || [])
			.map(condition => ({ value: condition, name: QuestStep.conditions[condition] }))
			.sort((a, b) => a.name.localeCompare(b.name));
		conditionsList.unshift({ value: "NA", name: "None" });
		
		// render multiple selection
		let content = [];
		for(let i = 0, max = data.length; i <= max; i++) {

			const dataIndex = data && data[i] || {};

			const conditionTypesList = (this.state.conditionTypes[dataIndex.name] || [])
				.map(conditionType => ({
					value: conditionType,
					name: QuestStep.conditionTypes[dataIndex.name][conditionType]
				}))
				.sort((a, b) => a.name.localeCompare(b.name));
			const showTypesList = dataIndex.name && conditionTypesList.length > 0;


			content.push(
				<div className="row">
					<label>
						{nameFieldLabel}
						<select value={dataIndex.name ?? ""} onChange={(event) => handleInputChange(i, 'name', event.target.value)}>
							<option value="" disabled>Please select</option>
							{conditionsList.map(({value, name}) => <option key={value} value={value}>{name}</option>)}
						</select>
						{this.renderError(errors[i] && errors[i].name)}
					</label>
				</div>
			);
			if(showTypesList) {
				content.push(
					<div className="row">
						<label>
							Clarify type of {QuestStep.conditions[dataIndex.name]} condition above
							<select value={dataIndex.type ?? ""} onChange={(event) => handleInputChange(i, 'type', event.target.value)}>
								<option value="" disabled>Please select</option>
								{conditionTypesList.map(({value, name}) => <option key={`type-${value}`} value={value}>{name}</option>)}
							</select>
							{this.renderError(errors[i] && errors[i].type)}
						</label>
					</div>
				);
			}
			appendContentCallback && content.push(appendContentCallback(i, dataIndex));
			if(dataIndex.name === "NA") {
				// NA is just local dummy for blank, if selected then do not add following controls
				break;
			}
			if(i < max) {
				// TODO use CSS
				content.push(<hr />);
			}
		}

		return content.length > 2? <fieldset>{content}</fieldset> : content;
	}

	renderFamilyConditions() {
		const data = this.state.data.familyConditions;
		const errors = this.state.errors.familyConditions;

		return this.renderConditions(
			data,
			errors,
			this.handleFamilyConditionsInputChange,
			'Do your relatives have any of these disease?',
			(index, dataIndex) => {
				const relationshipsList = (this.state.relationships || [])
					.map(value => ({
						value: value,
						name: QuestStep.relationships[value]
					}))
					.sort((a, b) => a.name.localeCompare(b.name));

				let appendContent = null;
				if(dataIndex && dataIndex.name && dataIndex.name !== 'NA') {
					return (
						<Fragment>
							<div className="row">
								<label>
									Who has/had this disease?
									<select value={dataIndex.relationshipLevel ?? ""} onChange={(event) => this.handleFamilyConditionsInputChange(index, 'relationshipLevel', event.target.value)}>
										<option value="" disabled>Please select</option>
										{relationshipsList.map(({value, name}) => <option key={`type-${value}`} value={value}>{name}</option>)}
									</select>
									{this.renderError(errors[index] && errors[index].relationshipLevel)}
								</label>
							</div>
							<div className="row">
								<label>
									Age when the disease was diagnosed:
									<input type="number" min="0" placeholder="Years" value={dataIndex.ageOfConditionDetection ?? ""} onChange={(event) => this.handleFamilyConditionsInputChange(index, 'ageOfConditionDetection', event.target.value)} />
									{this.renderError(errors[index] && errors[index].ageOfConditionDetection)}
								</label>
							</div>
						</Fragment>
					);
				}
			}
		);
	}

	render() {
		const {data, hasErrors, errors} = this.state;

		return (
			<Fragment>
				<h3>Questionnaire</h3>
				<p>To create your personal prevention plan we need some information about you</p>
				<div className="form">
					{(hasErrors || (errors.api && errors.api.length > 0)) && (
						<div className="errors">
							Please check the form for errors
							{errors.api && errors.api.length > 0 && (
								<ul>
									{errors.api.map((errorMessage) => (
										<li className="error">API: {errorMessage}</li>
									))}
								</ul>
							)}
						</div>
					)}
					<div className="row">
						<label>What is your biological gender?</label>
						<div className="group-radio">
							<label>
								<input type="radio" name="gender" value="F" checked={data.gender == "F"} onChange={this.handleInputChange} />
								<span>Female</span>
							</label>
							<label>
								<input type="radio" name="gender" value="M" checked={data.gender == "M"} onChange={this.handleInputChange} />
								<span>Male</span>
							</label>
						</div>
						{this.renderError(errors.gender)}
					</div>
					<div className="row">
						<div className="group-columns">
							<label>
								Age
								<input type="number" min="0" name="age" placeholder="Years" value={data.age} onChange={this.handleInputChange} />
								{this.renderError(errors.age)}
							</label>
							<label>
								Weight
								<input type="number" min="0" name="weight" placeholder="Kg" value={data.weight} onChange={this.handleInputChange} />
								{this.renderError(errors.weight)}
							</label>
							<label>
								Height
								<input type="number" min="0" name="height" placeholder="Cm" value={data.height} onChange={this.handleInputChange} />
								{this.renderError(errors.height)}
							</label>
						</div>
					</div>
					<div className="row">
						<label>
							Your activity hours summary for past 7 days:
							<input type="number" min="0" step="0.1" name="activityHours" placeholder="Hours" value={data.activityHours} onChange={this.handleInputChange} />
							{this.renderError(errors.activityHours)}
						</label>
					</div>
					{/*<div className="row">
						<label>Pregnant</label>
						<div className="group-radio">
							<label>
								<input type="radio" name="pregnant" value="na" onChange={this.handleInputChange} />
								<span>None</span>
							</label>
							<label>
								<input type="radio" name="pregnant" value="yes" onChange={this.handleInputChange} />
								<span>Yes</span>
							</label>
							<label>
								<input type="radio" name="pregnant" value="no" onChange={this.handleInputChange} />
								<span>No</span>
							</label>
						</div>
					</div>*/}
					{this.renderSmoker()}
					{this.renderConditions(
						data.conditions,
						errors.conditions,
						this.handleConditionsInputChange,
						'Which medical conditions do you have or have you had?'
					)}
					{this.renderFamilyConditions()}
				</div>

				<button className="action" onClick={this.handleSubmit}>GET MY PERSONAL PLAN</button>

			</Fragment>
		);
	}
}