import React from 'react';
import PropTypes from 'prop-types';

//import {redirect} from 'react-micro-router';

import stepComponents from './steps';
import Api from '../../api.js';

export default class MobileApp extends React.Component {

	static STEP_WELCOME = "welcome";
	static STEP_QUEST = "quest";
	static STEP_PLAN = "plan";
	static STEP_VISIT = "visit";

	static propTypes = {
		onStepChange: PropTypes.func
	};

	currentStepComponent = React.createRef();
	iphonexContentTransitionElement = React.createRef();

	constructor(props) {
		super(props);

		this.state = {
			// steps history (undoable algorythm: https://redux.js.org/recipes/implementing-undo-history)
			history: {
				past: [],
				present: { step: MobileApp.STEP_WELCOME, state: null },
				future: []
			},

			isLoadingCounter: 0
		};
	}

	componentDidMount() {
		this.dispatchOnStepChange();

		this.iphonexContentTransitionElement.current.addEventListener("animationend", () => {
			this.iphonexContentTransitionElement.current.classList.remove("slide-left", "slide-right");
		});
	}

	componentDidUpdate(prevProps, prevState) {
		const {present} = this.state.history;

		if(present.step !== prevState.history.present.step && present.state) {
			// load saved step state into component
			this.currentStepComponent.current.setState(present.state);
		}
	}

	dispatchOnStepChange() {
		this.props.onStepChange && this.props.onStepChange(this.state.history.present.step);
	}

	fetchApi(action, fetchOptions) {
		this.setIsLoading(true);

		return new Promise((resolve, reject) => {
			Api.fetch(action, fetchOptions)
			.then(result => {
				this.setIsLoading(false);
				resolve(result);
			})
			.catch(e => {
				console.log(e);
				this.setIsLoading(false);
			});
		});
	}

	setIsLoading = (isLoading) => {
		this.setState((prevState, props) => ({
			isLoadingCounter: prevState.isLoadingCounter + (isLoading? 1 : -1)
		}));
	}

	isLoading = () => {
		return this.state.isLoadingCounter > 0;
	}

	/**
	* Switch next step component
	*/
	setStep = (step, state = null) => {
		this.setState(prevState => {
			return {
				history: {
					...prevState.history,
					past: [
						...prevState.history.past,
						{
							step: prevState.history.present.step,
							state: this.currentStepComponent.current.state
						}
					],
					present: {
						step: step,
						state: state
					},
					future: [] // clear future steps
				}
			};

		}, () => {
			this.dispatchOnStepChange();
			this.addContentAnimation('slide-left');
		});
	}

	navigateBack = (e) => {
		e.preventDefault();

		const {past, present, future} = this.state.history;
		const newPresent = {...present, state: this.currentStepComponent.current.state};

		this.setState({
			history: {
				past: past.slice(0, past.length - 1),
				present: past[past.length - 1],
				future: [newPresent, ...future]
			}
		}, () => {
			this.dispatchOnStepChange();
			this.addContentAnimation('slide-right');
		});
	};

	navigateForward = (e) => {
		e.preventDefault();
		
		const {past, present, future} = this.state.history;
		const newPresent = {...present, state: this.currentStepComponent.current.state};

		this.setState({
			history: {
				past: [...past, newPresent],
				present: future[0],
				future: future.slice(1)
			}
		}, () => {
			this.dispatchOnStepChange();
			this.addContentAnimation('slide-left');
		});
	};

	/*
	* Clear state.history.future to disable forward navigation
	*/
	clearFutureHistory = () => {
		this.setState({
			history: {
				...this.state.history,
				future: []
			}
		});
	}

	addContentAnimation(cssAnimation) {
		this.iphonexContentTransitionElement.current.classList.add(cssAnimation);
	}

	getStepView() {
		const {step} = this.state.history.present;
		const Component = stepComponents[step.charAt(0).toUpperCase() + step.slice(1) + 'Step'];
		
		return <Component
			ref={this.currentStepComponent}

			setStep={this.setStep}
			clearFutureHistory={this.clearFutureHistory}

			fetchApi={this.fetchApi}
			setIsLoading={this.setIsLoading}
			isLoading={this.isLoading}
		/>;
	}

	render() {
		const {history} = this.state;
		const {step} = history.present;
		const {navigateBack, navigateForward} = this;
		const isLoading = this.isLoading();

		const canNavBack = history.past.length > 0;
		const canNavForward = history.future.length > 0;
		
		return (
			<div className={`mobile-app step-${step}`}>
				<div className="iphonex">
					<div className="front">
						<div className="screen">
							<div className="screen__view">
								<div className="intro-logo"></div>
								{step !== MobileApp.STEP_WELCOME &&
									<header>
										{!isLoading && canNavBack && <a className="undo" onClick={navigateBack}>&lt; back</a>}
										{!isLoading && canNavForward && <a className="redo" onClick={navigateForward}>forward &gt;</a>}
									</header>
								}
								<div className="content-transition" ref={this.iphonexContentTransitionElement}>
									<div className="content" id="iphonex-content">
										{isLoading && <div className="loading">LOADING...</div>}
										{this.getStepView()}
									</div>
								</div>
							</div>
							<div className="screen__front">
								<div className="screen__front-speaker"></div>
								<div className="screen__front-camera"></div>
							</div>
						</div>
						<div className="front__line"></div>
						<div className="front__line front__line-second"></div>
					</div>
					<div className="phoneButtons phoneButtons-right" id="iphonex-button-right"></div>
					<div className="phoneButtons phoneButtons-left"></div>
					<div className="phoneButtons phoneButtons-left2"></div>
					<div className="phoneButtons phoneButtons-left3"></div>
					<div className="back"></div>
				</div>
			</div>
		);
		
	}
}