import React from 'react';
import PropTypes from 'prop-types';
import MobileApp from '../MobileApp.jsx';

export default class PlanStep extends React.Component {

	/*static propTypes = {
		data: PropTypes.array
	};*/
	static screeningTypes = {
		CERVICAL_SMEAR_SCREENING: "Cervical Smear Screening",
		CHOLESTEROL_SCREENING: "Cholesterol Screening",
		DIABETES_SCREENING: "Diabetes Screening",
		COLONOSCOPY: "Colonoscopy Screening",
		FECAL_OCCULT_BLOOD_TEST: "Fecal Occult Blood Test",
		HYPERTENSION_SCREENING: "Hypertension Screening"
	};
	state = {
		data: null,
		showScreeningNote: {}
	};

	showScreeningNoteClick = (screeningType, show) => {
		this.setState(prevState => ({
			showScreeningNote: {
				...prevState.showScreeningNote,
				[screeningType]: show ?? !prevState.showScreeningNote[screeningType]
			}
		}));
	}

	handleBookClick(screeningType) {
		this.props.setStep(MobileApp.STEP_VISIT, { screeningType: screeningType });
	}

	render() {
		const {data} = this.state;
		const {showScreeningNote} = this.state;

		return (
			<React.Fragment>
				<h3>Personal Prevention Plan</h3>
				<p>Your personal prevention plan includes all the screenings and medical checks required to prevent potential disease.</p>

				<div className="booking-list">
					{data && data.length ? data.map(item => {
						const type = item.screeningType;
						return (
							<div className="item" key={type}>
								<h4>{PlanStep.screeningTypes[type]}</h4>
								<a onClick={() => this.showScreeningNoteClick(type)}>Why is it relevant to me?</a>
								{showScreeningNote[type] && (
									<div className="note">{item.relevanceNote}</div>
								)}
								<div className="controls">
									<button onClick={() => this.handleBookClick(type)}>Book now</button>
									<button disabled>Mark as done</button>
								</div>
							</div>
						);
					}) : "No recommendations found"}
				</div>
				
			</React.Fragment>
		);
	}
}