import React from 'react';
import {Route, Link, isMatch} from 'react-micro-router';
import MobileApp from '../mobile-app/MobileApp';

import logo from '../../assets/logo-text.png';
import logoWhite from '../../assets/logo-white.png';
import logoBigFull from '../../assets/logo-big-white.png';
import contactAvatar from '../../assets/contact-avatar.jpg';
import '../../assets/App.scss';

export default class App extends React.Component {

	state = {
		mobileAppStep: null
	};

	onAppStepChange = (step) => {
		this.setState({ mobileAppStep: step });
	}

	renderDemoDescription() {
		switch(this.state.mobileAppStep) {
			case MobileApp.STEP_WELCOME:
				return {
					description: (
						<React.Fragment>
							<img src={logoWhite} />
							<p>PriMedical is evidence-based preventive healthcare service helping its users to stay healthy by analyzing their biomarkers and giving them actionable health recommendations.</p>
						</React.Fragment>
					)
				};
				break;

			case MobileApp.STEP_QUEST:
				return {
					description: (
						<React.Fragment>
							<h3>1. QUESTIONNAIRE</h3>
							<p>Customer takes a short survey<sup>1</sup>.</p>
							<p>We get customer’s initial health data in order to generate a Personal Prevention Plan (PPP).</p>
							<p className="small">
								For the purpose of MVP:
								<ul>
									<li>The number of initial questions is limited to 8 (the full questionnaire takes 2 minutes to answer).</li>
									<li>The user does not connect any wearable.</li>
									<li>The user does not choose his health goals (required later for generation of Health Programs).</li>
								</ul>
							</p>
							<p className="small">
								Note:
								<ul>
									<li>The survey will be done in a form of a chat bot (Lemonade app as an example);</li>
								</ul>
							</p>
						</React.Fragment>
					),
					hints: (
						<React.Fragment>
							<div><sup>1</sup>Questionnaire is the very first step that helps us to get the initial health data. Through the customer journey, the customer will be asked to add other health data sources.</div>
						</React.Fragment>
					)
				};
				break;

			case MobileApp.STEP_PLAN:
				return {
					description: (
						<React.Fragment>
							<h3>2. PERSONAL PREVENTION PLAN</h3>
							<p>The customer gets his Personal Prevention Plan (PPP).</p>
							<p>PPP is a list of medical screenings, check-ups and recommendations relevant to the customer. These recommendations are based on US PSTF<sup>1</sup> adopted to the European market.</p>
							<p>Following the PPP recommendations helps to prevent potential diseases.</p>
							<p className="small">
								For the purpose of MVP:
								<ul>
									<li>The PPP is based only on the data collected via the questionnaire<sup>2</sup>;</li>
								</ul>
							</p>
							<p className="small">
								Note:
								<ul>
									<li>“Mark as done” button will include manual input of results / OCR / device recognition etc. – this data will be used for further analysis;</li>
									<li>“Why is it relevant to me?” – explanation why the recommendation appeard in my PPP.</li>
								</ul>
							</p>
						</React.Fragment>
					),
					hints: (
						<React.Fragment>
							<div><sup>1</sup>US PSTF – US Preventive Services Task Force – internationally recognized panel of experts in primary care, prevention and research methods that makes evidence-based recommendations to guide the delivery of clinical preventive services.</div>
							<div><sup>2</sup>Apart from questionnaire the data will be taken from wearables, health trackers, smart gadgets, health apps, EMRs (electronic medical records), medical tests, symptoms checkers, data retrieved from communication with general practitioners, coaches, etc. leading to better recommendations.</div>
						</React.Fragment>
					)
				};
				break;

			case MobileApp.STEP_VISIT:
				return {
					description: (
						<React.Fragment>
							<h3>3. VISIT SCHEDULING, PAYMENT</h3>
							<p>The customer gets a choice of 3 relevant doctors, schedules a visit and makes a payment.</p>
							<p className="small highlight">
								For further steps proper integrations required:
								<ul>
									<li>ZnanyLekarz or direct integration with healthcare provider like LuxMed, Medicover etc.</li>									
									<li>Diag.pl</li>
									<li>Payment system</li>
								</ul>
							</p>
							<p className="small">
								For the purpose of MVP:
								<ul>
									<li>The functionality of this page is limited to “Book now” button;</li>
								</ul>
							</p>
							<center>
								<Link to="/contacts">
									<button>CONTACT US</button>
								</Link>
							</center>
						</React.Fragment>
					)
				};
				break;
		}
	}

	renderDemo() {
		const description = this.renderDemoDescription();

		return (
			<div className="page-demo">
				<MobileApp onStepChange={this.onAppStepChange} />

				<div className="description">
					{description && description.description}
				</div>

				{description && description.hints &&
					<footer className="description-hints">
						{description.hints}
					</footer>
				}
			</div>
		);
	}

	renderContacts() {
		return (
			<div className="page-contacts">
				<header><img src={logoBigFull} /></header>
				<div className="contacts">
					<img src={contactAvatar} />
					<h3>Zenia Karastoyanov</h3>
					<ul>
						<li>
							<strong>Tel.</strong>: <a href="tel:+48 797 898 978">+48 797 898 978</a>
						</li>
						<li>
							<strong>Email</strong>: <a href="mailto:e.karastoyanov@gmail.com">e.karastoyanov@gmail.com</a>
						</li>
					</ul>
				</div>
			</div>
		);
	}

	render() {
		const {mobileAppStep} = this.state;

		return (
			<div className={`app step-${mobileAppStep}`}>
				<header className="app-header">
					<Link to="/" className="logo">
						<img src={logo} alt="PriMedical" onClick={() => isMatch("/", true) && window.location.reload()} />
					</Link>
					
					<div className="navigation">
						<Route path="/" exact>
							<Link to="/contacts">Contact us</Link>
						</Route>
						<Route path="/contacts">
							<Link to="/">Go to demo</Link>
						</Route>
					</div>
				</header>

				<div>
					<Route path="/" exact>
						{this.renderDemo()}
					</Route>
					<Route path="/contacts">
						{this.renderContacts()}
					</Route>
				</div>
				<div>{/* Last div for CSS flex to position content center */}</div>
			</div>
		);
	}
}
