import React from 'react';
import PropTypes from 'prop-types';
import PlanStep from './Plan.jsx';
import MobileApp from '../MobileApp.jsx';

export default class VisitStep extends React.Component {

	state = {
		screeningType: null, // comes from Plan step
		data: null,
		isBooked: false
	};

	componentDidUpdate(prevProps, prevState) {
		const {screeningType} = this.state;

		if(prevState.screeningType !== screeningType) {
			this.props.fetchApi('/visits', {data: {screeningType: screeningType}})
				.then(result => this.setState({ data: result }));
		}
	}

	handleBookClick = () => {
		// TODO receive visit ID (not implemented on server yet)
		this.setState({ isBooked: true });
	}

	render() {
		const {data, isBooked} = this.state;

		if(isBooked) {
			return (
				<React.Fragment>
					<h3>Under construction</h3>
					<p>For further steps integration with ZnanyLekarz is required</p>
				</React.Fragment>
			);
		}

		return (
			<React.Fragment>
				<h3>Schedule my visit</h3>
				<p>Below is the most relevant doctors or medical tests based on your unique medical needs.</p>

				<div className="booking-list">
					{data && data.length ? data.map(item => {
						
						let name = 'Unknown visit type';
						let subject = item.locationName;
						if(item.visitType === "LAB_TEST") {
							name = PlanStep.screeningTypes[item.visitName] || item.visitName;
						}
						else {
							name = `Dr. ${item.doctor.name} ${item.doctor.surname}`;
						}
						subject += ` | ${item.visitPrice} PLN`;

						return (
							<div className="item">
								<h4>{name}</h4>
								{subject}
								<div className="controls">
									<button onClick={this.handleBookClick}>Book now</button>
								</div>
							</div>
						);
					}) : "No visits found"}
				</div>
				
			</React.Fragment>
		);
	}
}