export default class Api {

	static host = 'http://primedical.co/api/v1/screenings';

	static async fetch(action = '', fetchOptions = []) {
		fetchOptions = {

			method: 'GET',
			headers: {},

			...fetchOptions
		};

		let qs = '';
		if(fetchOptions.data) {
			if (['GET', 'DELETE'].indexOf(fetchOptions.method) > -1) {
				qs = '?' + Object.keys(fetchOptions.data)
							.map(key => encodeURIComponent(key) + '=' + encodeURIComponent(fetchOptions.data[key]))
							.join('&');
			}
			else {
				fetchOptions.body = JSON.stringify(fetchOptions.data);
				fetchOptions.headers['Content-Type'] = "application/json";
			}
			delete fetchOptions.data;
		}

		const url = Api.host + action + qs;

		//console.log(url, fetchOptions);

		return fetch(url, fetchOptions)
			.then(response => response.json());
			//.then(result => console.log('api resilt', result));
	}
}